import { Auth, AuthProvider, signInWithPopup, User  } from "firebase/auth";
import { call, put } from "redux-saga/effects";
import { userProfileSlice } from "../store/userProfile";
import { mapFirebaseUser } from "../utilities/mapFirebaseUser";

export const authenticateFirebaseWithProviderEffect = function*(auth: Auth, authProvider: AuthProvider) {
  yield call(signInWithPopup, auth, authProvider);
}

export const writeUserToStateEffect = function*(user: User) {
  const userProfile = mapFirebaseUser(user.providerData[0]);
  yield put(userProfileSlice.actions.setUserProfileState(userProfile));
}
