import { 
  CollectionReference, 
  DocumentData, 
  getDocs, 
  query, 
  where, 
  QueryDocumentSnapshot, 
  Query, 
  documentId,  
  limit
} from "firebase/firestore";
import { IHaveId, id } from "../types/domain";
import { IExternalResourceFilters, searchFields } from "../store/externalResourceSlice";
import { OwnerIdFields } from ".";
import { getNextSearchKey } from "./getNextSearchKey";

const maxDocumentCount = 200;

export const executeQuery = async (
  documentQuery: Query<DocumentData>
): Promise<IHaveId[]> => {
  const limitedQuesry = query(documentQuery, limit(maxDocumentCount));

  const docs = await getDocs(limitedQuesry);

  const snashot: IHaveId[] = [];
  docs.docs.forEach((currentDocument, index) => {
    const documentData = transformFirebaseDocudent(currentDocument);
    snashot.push(documentData);
  });
  return snashot;
}

export const queryCollectionWithFilters = async (
  collection: CollectionReference,
  {ids, startDate, search, endDate}: Partial<IExternalResourceFilters> = {},
  ouwnerIdField: OwnerIdFields = OwnerIdFields.ouwner,
  ouwnerIdFieldValue: id = '',
): Promise<IHaveId[]> => {
  let documentQuery = query(collection);

  if(ouwnerIdField && ouwnerIdFieldValue){
    documentQuery = query(documentQuery, where(ouwnerIdField, "==", ouwnerIdFieldValue));
  }

  if(ids && ids.length === 0){
    return [];
  }

  if(ids && ids.length){
    documentQuery = query(documentQuery, where(documentId(), "in", ids));
  }

  if(startDate && endDate){
    documentQuery = query(documentQuery, where("date", ">=", startDate));
    documentQuery = query(documentQuery, where("date", "<=", endDate));
  }

  let documentQuerys: Query<DocumentData>[] = [];

  if(search){
    documentQuerys = searchFields.map((field: string) => {
      const nextSearchKey: string = getNextSearchKey(search);
      const fieldQuery = query(documentQuery, where(field, ">=", search), where(field, "<", nextSearchKey));
      return fieldQuery;
    }); 
  }


  if(!!documentQuerys.length){
    const docs = await Promise.all(documentQuerys.map((documentQuery) => executeQuery(documentQuery)));

    const uniqueDocs = Array.from(new Set(docs.flat().map(doc => JSON.stringify(doc)))).map(doc => JSON.parse(doc));

    return uniqueDocs;

  } else {
    const docs = await executeQuery(documentQuery);  
    return docs;
  }
}

export const transformFirebaseDocudent = (currentDocument: QueryDocumentSnapshot<DocumentData>): IHaveId => {
  const data = currentDocument.data();
  const id = currentDocument.id;
  return {
    id,
    ...data,
  }
}
