import { addDoc, collection, deleteDoc, doc, Firestore, getDoc, setDoc } from "firebase/firestore";
import { IExternalResourceFilters } from "../store/externalResourceSlice";
import { IHaveId, id } from "../types/domain";
import { queryCollectionWithFilters } from "./queryCollection";
import { CreateType } from "./types";

export interface ICollectionHandeler<Data extends IHaveId> {
  create: (createData: CreateType<Data>) => Promise<id>;
  update: (updateData: Data & IHaveId) => Promise<void>;
  delete: (id: id) => Promise<void>;
  list: (ouwnerIdFieldValue: id, filters?: Partial<IExternalResourceFilters>) => Promise<Data[]>;
  single: (id: id) => Promise<Data>;
}

export enum OwnerIdFields {
  teacherId = 'teacherId',
  studentId = 'studentId',
  ouwner = 'id',
} 

export const createCollectionHandeler = <Data extends IHaveId>(
  db: Firestore, 
  collectionName: string, 
  ouwnerIdField: OwnerIdFields = OwnerIdFields.ouwner,
): ICollectionHandeler<Data> => {
  const collectionRef = collection(db, collectionName);
  return {
    create: async (createData) => {
      const documentRef = await addDoc(collectionRef, createData);
      const newDocId = documentRef.id;
      return newDocId;
    },
    update: async ({id, ...rest}) => {
      const document = doc(db, collectionName, id);
      await setDoc(document, rest);
    },
    delete: async (id) => {
      const document = doc(db, collectionName, id);
      await deleteDoc(document);
    },
    list: async (ownerId, filters = {}) => {
      const data = await queryCollectionWithFilters(collectionRef, filters, ouwnerIdField, ownerId) as Data[];
      return data;
    },
    single: async (id) => {
      const document = doc(db, collectionName, id);
      const documentReference = await getDoc(document);
      const documentData = documentReference.data();

      const fullDocument = {
        ...documentData,
        id,
      } as Data;
        return fullDocument;   
    }
  }
};

export const createEmptyCollectionHandeler = <Data extends IHaveId>(db: Firestore, collectionName: string): ICollectionHandeler<Data> => {
  return {
    create: async (createData) => {
      return '42';
    },
    update: async ({id, ...rest}) => {
      
    },
    delete: async (id) => {
      
    },
    list: async () => {
      return [];
    },
    single: async (id) => {
      return { id: '42' } as Data;
    }
  }
};
