import { IErrorDetails } from "../store/errorSlice";
import { id } from "../types/domain";
import { ArgumentSelector, CommonSelector, FirebaseUserInfo } from "../types/utility";

export const selectLoading: ArgumentSelector<CommonSelector<boolean>> = (comandName: string) => {
  return (state) => !!state.loading[comandName];
}

export const selectError: ArgumentSelector<CommonSelector<IErrorDetails | undefined>> = (comandName: string) => {
  return (state) => state.error[comandName];
}

export const selectHasError: ArgumentSelector<CommonSelector<boolean>> = (comandName: string) => {
  return (state) => !!state.error[comandName];
}

export const selectUserProfile: CommonSelector<FirebaseUserInfo> = (state) => {
  return state.userProfile;
}

export const selectIsUserAuthenticated: CommonSelector<boolean> = (state) => {
  return !!selectUserProfile(state).id;
}

export const selectAuthenticatedUserId: CommonSelector<id> = (state) => {
  return selectUserProfile(state).id;
}

export const selectUserProfileAvatar: CommonSelector<string> = (state) => {
  return selectUserProfile(state).photoURL || '';
}

export const selectUserDisplayName:CommonSelector<string> = (state) => {
  return selectUserProfile(state).displayName || '';
}

export const selectUserProfilePhotoUrl: CommonSelector<string> = (state) => {
  return selectUserProfile(state).photoURL || '';
}