import { User } from "firebase/auth";
import { createCommand } from "../utilities/createCommand";
import { authenticateFirebaseWithProviderEffect, writeUserToStateEffect } from "./authenticateFirebaseWithProviderEffect";
import { CallEffect, select } from "redux-saga/effects";
import { auth, facebookAuthProvider, googleAuthProvider } from "../api/firebaseConnection";
import { selectUserProfile } from "../selectors/commonSelectors";
import { FirebaseUserInfo } from "../types/utility";
import { IHaveId } from "../types/domain";

interface AppSpecificAuthCommands {
  alreadyAuthenticatedUser: any;
  authenticateWithFirebase: any;
  authenticateFacebook: any;
}

type createEntityFromUserEffect = (user: User) => Generator<CallEffect<void>, void, unknown>;
type createEntityFromProfileEffect = (user: FirebaseUserInfo) => Generator<CallEffect<void> | CallEffect<IHaveId>, void, IHaveId>;

export const makeAppSpecificCommands = (
  createNewEntityEffect: createEntityFromUserEffect,
  createNewEntiryFromProfile: createEntityFromProfileEffect
): AppSpecificAuthCommands => {
  const alreadyAuthenticatedUser = createCommand<User>(
    'alreadyAuthenticatedUser',
    function*({payload: user}) {
      console.log('alreadyAuthenticatedUser', user);
      
      yield writeUserToStateEffect(user);
      yield createNewEntityEffect(user);
    }
  )
  
  const authenticateWithFirebase = createCommand<void>(
    'authenticateWithFirebase',
    function* () {
      console.log('authenticateWithFirebase')
      yield authenticateFirebaseWithProviderEffect(auth, googleAuthProvider);
      const userProfile: FirebaseUserInfo = yield select(selectUserProfile);
      yield createNewEntiryFromProfile(userProfile);
    },
  );
  
  const authenticateFacebook = createCommand<void>(
    'authenticateWithFacebook',
    function*() {
      yield authenticateFirebaseWithProviderEffect(auth, facebookAuthProvider);
      const userProfile: FirebaseUserInfo = yield select(selectUserProfile);
      yield createNewEntiryFromProfile(userProfile);
    }
  )

  return {
    alreadyAuthenticatedUser,
    authenticateWithFirebase,
    authenticateFacebook,
  }  
}
