import { mapDataBaseRuleToRule } from "../store/utils";
import { 
  IRule, id, 
} from "../types/domain";
import { ISubjectView, ITeacherView } from "../types/teacherDomainView";
import { IDataBaseRule, getDomainHandelers } from "./handelers";

export const domainHandelers = getDomainHandelers();

export const readSubjectViewApiCall = async (id: id): Promise<ISubjectView | undefined> => {
  const subjectEntity = await domainHandelers.subjectApiHandeler.single(id);

  if (!!subjectEntity.id) {
    const rulesIds = subjectEntity.rules;
    const rulesDatabase = await domainHandelers.rulesApiHandeler.list('', { ids: rulesIds });
    const rulesEntities = rulesDatabase.map(mapDataBaseRuleToRule);
    return {
      ...subjectEntity,
      rules: rulesEntities,
    }
  } else {
    return {
      id: '',
      name: '',
      description: '',
      rules: [],
      teacherId: '',
    }
  }
};


export const readTeacherViewApiCall = async (id: id): Promise<ITeacherView> => {
  const data = await domainHandelers.teacherAPIHandeler.single(id);
  const { abonementOffers = [], subjects = [], ...rest } = data || {};

  const abonementsData = await domainHandelers.abonementOffer.list(id, { ids: abonementOffers });
  const subjectsData = await domainHandelers.subjectApiHandeler.list(id, { ids: subjects });
  const subjectRulesIds = subjectsData.flatMap(({rules}) => rules);
  const rules = await domainHandelers.rulesApiHandeler.list(id, {ids: subjectRulesIds});

  const subjectViews = subjectsData.map((subject): ISubjectView => {
    const thisSubjectRules = subject.rules.map((id) => rules.find(({id: itemId}) => itemId === id));
    const viewDataBaseRules = thisSubjectRules.filter((item) => !!item) as IDataBaseRule[];
    const viewRules: IRule[] = viewDataBaseRules.map(mapDataBaseRuleToRule);

    return {
      ...subject,
      rules: viewRules,
    }
  })

  const teacherView: ITeacherView = {
    ...rest,
    abonementOffers: abonementsData,
    subjects: subjectViews,
  }

  return teacherView;
};
