import { Firestore, Timestamp } from "firebase/firestore";
import { OwnerIdFields, createCollectionHandeler } from ".";
import { database } from "./firebaseConnection";
import { IAbonementOffer, ILesson, IRule, IStudent, ISubject, ITeacher, IUser } from "../types/domain";
import { DataBaseCollections, EnvPrefixes } from "./types";

type OmitDateLesson = Omit<ILesson, 'date' >;

export interface IDataBaseLesson extends OmitDateLesson {
  date: Timestamp;
};

type OmitDateRule = Omit<IRule, 'timeStart' >;

export interface IDataBaseRule extends OmitDateRule {
  timeStart: Timestamp;
}

const createAPIHandelers = (db: Firestore, prefix: string = "", collectionHandelerCreator = createCollectionHandeler) => {
  return {
    teacherAPIHandeler: collectionHandelerCreator<ITeacher>(db, `${prefix}${DataBaseCollections.teacher}`, OwnerIdFields.ouwner),
    studentAPIHandeler: collectionHandelerCreator<IStudent>(db, `${prefix}${DataBaseCollections.student}`, OwnerIdFields.ouwner),
    subjectApiHandeler: collectionHandelerCreator<ISubject>(db, `${prefix}${DataBaseCollections.subject}`, OwnerIdFields.teacherId),
    rulesApiHandeler: collectionHandelerCreator<IDataBaseRule>(db, `${prefix}${DataBaseCollections.rules}`, OwnerIdFields.teacherId),
    abonementOffer: collectionHandelerCreator<IAbonementOffer>(db, `${prefix}${DataBaseCollections.abonementOffer}`, OwnerIdFields.teacherId),
    userAPIHandeler: collectionHandelerCreator<IUser>(db, `${prefix}${DataBaseCollections.users}`, OwnerIdFields.teacherId),
    lessonAPIHandeler: collectionHandelerCreator<IDataBaseLesson>(db, `${prefix}${DataBaseCollections.lessons}`, OwnerIdFields.teacherId),
  }
}

const productionHandelers = createAPIHandelers(database, EnvPrefixes.production);
const devHandelers = createAPIHandelers(database, EnvPrefixes.dev);
const testHandelers = createAPIHandelers(database, EnvPrefixes.test);

export const getDomainHandelers = (): ReturnType<typeof createAPIHandelers> => {
  const nodeEnv = process.env.NODE_ENV;
  const devEnv = process.env.REACT_APP_DEVELOPMENT === 'true';

  if (devEnv) {
    return devHandelers;
  }

  switch (nodeEnv) {
    case 'production': return productionHandelers;
    case 'development': return devHandelers;
    case 'test': return testHandelers;
    default: return testHandelers;
  }
}
